import React, {Component} from "react";
import {connect} from "react-redux";
import {getFrontValidationMsg, getDeliveryAndPaymentStep} from "../selectors";

import ScrollableComponent from "eshop/modules/checkout/components/ScrollableComponent";

class MulticartValidationComponent extends ScrollableComponent {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        super.componentDidMount();
    }

    componentWillUpdate() {
        super.componentWillUpdate();
    }

    componentDidUpdate() {
        super.componentDidUpdate()
    }

    componentWillUnmount() {
        super.componentWillUnmount();
    }


    render() {
        const error = this.props.show && this.props.errorList && this.props.errorList.find(e => this.props.messageType === e.type);
        if (!error) {
            return <div className={"VALIDATION_COMPONENT"+this.state.no}></div>;
        }

        let icon="g-icon--"+this.props.iconType;
        let errorMessage = this.props.messageText || error.message

        return (
            <div className={"o-icon-text g-icon  g-icon--before g-icon--xs-s g-redf-c u-padding-top-s  "+icon+" VALIDATION_COMPONENT"+this.state.no + this.props.className} ref="errorDiv">
                <p className={"o-icon-text__text u-font-bold opl-msg--error opl-msg--error-anchor "+(this.props.scrollToParent&&"opl-msg-scrollToParent") } data-scroll-offset={this.props.scrollOffset}>{errorMessage}</p>
            </div>);
    }
}



const mapStateToProps = state => ({
    errorList: getFrontValidationMsg(state),
    isDeliveryAndPaymentStep: getDeliveryAndPaymentStep(state)

});


const mapDispatchToProps = dispatch => ({
    //dummy
});

MulticartValidationComponent.defaultProps = {
    show: true,
    className: "",
    scrollOffset: 0,
    iconType:"error"
};

export default connect(mapStateToProps, mapDispatchToProps)(MulticartValidationComponent);